import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController,
  ModalController
} from "ionic-angular";
import { ExecGroupFunctionViewModel } from "../../Model/ViewModel/ExecGroupFunctionViewModel";

/**
 * Generated class for the programModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-batch-exec-program-add-edit-modal",
  templateUrl: "batch-exec-program-add-edit-modal.html"
})
export class BatchExecProgramAddEditModalPage {
  item: ExecGroupFunctionViewModel;
  mode: string;
  CanEditBatch:boolean;
  title:string;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public modalCtrl: ModalController
  ) {
    this.title="批次程式";
    this.item = new ExecGroupFunctionViewModel();
    this.item.program_func_no=navParams.data.item.program_func_no;
    this.item.ssis_program_set_no=navParams.data.item.ssis_program_set_no;
    this.item.exec_group=navParams.data.item.exec_group;
    this.item.note=navParams.data.item.note;
    this.item.is_active=navParams.data.item.is_active;
    this.item.creator=navParams.data.item.creator;
    this.item.create_time=navParams.data.item.create_time;
    this.item.modifier=navParams.data.item.modifier;
    this.item.last_update_time=navParams.data.item.last_update_time;
    this.item.ssis_program_set_desc = navParams.data.item.ssis_program_set_desc;
    this.CanEditBatch=navParams.data.CanEditBatch;
    this.mode = navParams.data.mode;
    console.log(this.CanEditBatch);
  }


  Save() {

    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }
  ionViewDidLoad() {
    console.log("ionViewDidLoad ProgramModalPage");
  }
}



