export class ExecGroupFunctionViewModel {
  /// <summary>
  ///  程式功能編號
  /// <summary>
  public program_func_no: string;
  /// <summary>
  ///  ssis程式設定編號
  /// <summary>
  public ssis_program_set_no: string;
  /// <summary>
  ///  轉檔群組
  /// <summary>
  public exec_group: string;
  /// <summary>
  ///  備註
  /// <summary>
  public note: string;
  /// <summary>
  ///  啟用註記
  /// <summary>
  public is_active: boolean;
  /// <summary>
  ///  建立者
  /// <summary>
  public creator: string;
  /// <summary>
  ///  建立時間
  /// <summary>
  public create_time: Date;
  /// <summary>
  ///  最後更新者
  /// <summary>
  public modifier: string;
  /// <summary>
  ///  最後更新時間
  /// <summary>
  public last_update_time: Date;
  /// <summary>
  ///  程式說明
  /// <summary>
  public ssis_program_set_desc: string;
  public is_active_desc: string;

  /// <summary>
  ///  啟用顏色
  /// <summary>
  public is_active_color: string;

  /// <summary>
  ///  啟用顏色
  /// <summary>
  public is_active_color_right: string;
}



